import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { OffersService } from 'src/app/service/offers.service';
declare var swal: any;

@Component({
  selector: 'app-offers-add',
  templateUrl: './offers-add.component.html'
})
export class OffersAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;
  selectedFile: File;
  editImageUrl: any = null;

  showLoader = false;
  formErrors = {
    offer_name: null,
    apierror: null
  };
  isEditing = false;
  editId = null;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private offersservice: OffersService,
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      offer_name: [null, Validators.compose([Validators.required])],
      offer_url: [null, Validators.compose([Validators.required])],
      offer_details: [''],
      is_active:[true, Validators.compose([Validators.required])],
    });
  }

  ngOnInit() {
  }


  editObject() {
    this.loadingState = true;
    this.offersservice.getOffersById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.editImageUrl = response.data.offer_image,
          this.addForm.patchValue({
            offer_name: response.data.offer_name,
            offer_details: response.data.offer_details,
            is_active:response.data.is_active,
            offer_url: response.data.offer_url,
          });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('/offer');
    });
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].name.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
    }
  }

  removeFile() {
    this.selectedFile = null;
  }

  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      const formData = new FormData();
      formData.append('offer_url', formdata.value.offer_url);
      formData.append('offer_name', formdata.value.offer_name);
      formData.append('offer_details', formdata.value.offer_details);
      formData.append('is_active', formdata.value.is_active);

     
      if (this.selectedFile) {
        formData.append('file', this.selectedFile);
      }
      if (this.isEditing) {
        this.offersservice.editOffers(this.editId, formData).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/offer');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('offer_name')) {
                this.formErrors.offer_name = obj.offer_name;
              }
            });
          }
        });
      } else {
        this.offersservice.addOffers(formData).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/offer');
          } else {
            this.showLoader = false;
            response.error.map(obj => {
              if (obj.hasOwnProperty('offer_name')) {
                this.formErrors.offer_name = obj.offer_name;
              }

            });
          }
        });
      }
    }
  }
}

